var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',[_c('header-section'),_vm._ssrNode("<div class=\"terminos_section\" data-v-dfcd1b84><div class=\"terminos_wrapper\" data-v-dfcd1b84><h1 data-v-dfcd1b84>Términos y Condiciones.</h1><aside data-v-dfcd1b84><p data-v-dfcd1b84>MAKEASY S.R.L. empresa legalmente constituida en Perú con número deidentificación tributaria RUC 20602768059, brinda la plataforma EASYBILL como servicio de Facturación Electrónica. </p></aside><h2 data-v-dfcd1b84>Términos</h2><ul data-v-dfcd1b84><li data-v-dfcd1b84>El término contrato hace referencia al acuerdo entre las dos partes.</li><li data-v-dfcd1b84>El término sitio web hace referencia al sitio donde presta losservicios, que se puede acceder con el usuario principal o secundariogenerado.</li><li data-v-dfcd1b84>El término servicio se refiere a la aplicación en el sitio web condominio app.easybill.pe.</li><li data-v-dfcd1b84>El término usuario principal hace referencia a la empresa con RUCque se utilizó para registrarse su cuenta.</li><li data-v-dfcd1b84>El término usuario secundario hace referencia a la persona designadacomo administrador o trabajador por el Gerente de la empresa.</li><li data-v-dfcd1b84>El término Plan hace referencia a las características y beneficios quetiene la empresa según el pago que efectúe.</li></ul><h2 data-v-dfcd1b84>Aceptación</h2><aside data-v-dfcd1b84><p data-v-dfcd1b84>El usuario principal de EASYBILL acepta lo dispuesto en este contrato y ensus Términos y condiciones. El usuario principal que no esté de acuerdo con estos términos y condiciones no podrá hacer uso del servicio EASYBILL prestado por MAKEASY S.R.L.</p><p data-v-dfcd1b84>La aceptación se desarrolla con el contrato entre las dos partes, se celebrade dos modalidades: presencial con un contrato físico y virtual adjuntando el contrato firmado.</p></aside><h2 data-v-dfcd1b84>Capacidad de Celebración de Contratos</h2><aside data-v-dfcd1b84><p data-v-dfcd1b84>La persona capacitada para celebrar el contrato es el representante legal dela empresa y se responsabiliza por las acciones que se realizan en EASYBILL.</p><p data-v-dfcd1b84>En caso de incumplimiento del contrato firmado de cualquiera de las dos partes se dará por terminado el contrato. Teniendo opción a resolver los inconvenientes de acuerdo a la gravedad del incumplimiento suscitado.</p></aside><h2 data-v-dfcd1b84>Registro de Cuenta y Usuario</h2><aside data-v-dfcd1b84><p data-v-dfcd1b84>MAKEASY S.R.L. presta servicio de Facturación Electrónica  que se accede através de nuestro sitio web www.easybill.pe Los usuarios principales y secundarios pueden acceder a nuestro servicio con sus credenciales brindados y generados por autorización del Gerente General de la empresa.</p><p data-v-dfcd1b84>EASYBILL permite el ingreso de una cantidad limitada de usuarios según el plan en que se realiza el pago, teniendo conocimiento de los planes y sus beneficios. Pueden tener acceso solo los usuarios autorizados con el nivel asignado en el momento de su creación, los usuarios no pueden ser reemplazados por otras personas que conozcan las credenciales de pasaresto no nos hacemos responsables por el uso impropio de la información encontra de la empresa. Además en caso de verse en este escenario y se tiene conocimiento sobre este hecho informar de inmediato para dar de baja al o los usuarios detectados.</p><p data-v-dfcd1b84>Los usuarios tienen acceso al sistema durante el mes de pago realizado osegún el plan elegido y pagado.</p><p data-v-dfcd1b84>Se brinda niveles de usuarios: administrador y trabajador. El usuario con nivel administrador puede acceder a todas las funcionalidades con la que cuenta su plan de suscripción, mientras que el usuario a nivel trabajador solo tendrá acceso a la sección ventas rápidas que le permitirá generar facturas y boletas electrónicas, así como algunas configuraciones básicos necesarias para realizar ventas.</p></aside><h2 data-v-dfcd1b84>Descripción del Servicio</h2><aside data-v-dfcd1b84><p data-v-dfcd1b84>MAKEASY brinda el servicio de Facturación Electrónica EASYBILL mediante dos modalidades gratuito y pagado. El plan gratuito puede usarlo durante un mes, que le permite probar todas las funcionalidades antes de elegir un plan. El usuario principal adquiere un permiso intransferible para usar el sistema con los beneficios del plan elegido, pero en ningún caso adquiere propiedad sobre el sistema.</p><p data-v-dfcd1b84>Los derechos de autor sobre las obras de software que componen el sistemason de titularidad de MAKEASY S.R.L. y bajo ningún motivo y momento de éstos términos se comprenderán como transferidos al usuario. El servicio consiste el uso del sistema EASYBILL disponible en app.easybill.pe.</p><p data-v-dfcd1b84>El usuario principal es conocedor del plan por el que está pagando, no nos hacemos responsables por los errores cometidos por el usuario en  el momento de elegir un plan ni de la información ingresada al usar el sistema.</p></aside><h2 data-v-dfcd1b84>Alcance y Nivel de Servicio</h2><aside data-v-dfcd1b84><p data-v-dfcd1b84>El sistema EASYBILL se encuentra disponible el 99% del tiempo, el 1% se toma para realizar mantenimiento y actualizaciones al sistema. Las diferentes fallas e inconvenientes ocasionados por agentes externos(conexión de internet, hardware, etc.) a EASYBILL no serán aceptadas bajo ningún concepto como responsabilidad de MAKEASY S.R.L. </p><p data-v-dfcd1b84>MAKEASY S.R.L. brindará información anticipada sobre actualizaciones del sistema EASYBILL.</p><p data-v-dfcd1b84>El representante legal de la empresa se compromete a informar el decesodel presente contrato con un promedio de 3 meses.</p></aside><h2 data-v-dfcd1b84>Medios de Pago</h2><aside data-v-dfcd1b84><p data-v-dfcd1b84>El pago que se realiza por el servicio de Facturación Electrónica es anticipado, el usuario principal mediante el presente acepta realizar el pago días antes de iniciar el siguiente mes de uso.</p><p data-v-dfcd1b84>EASYBILL cuenta con dos pasarelas de pago online: </p><p data-v-dfcd1b84>PAGO EFECTIVO: la pasarela de pago te permite generar un código CIP conel que puedes realizar el pago en cualquier agente más cercano a ti a nivel nacional. Para realizar el pago debes dirigirte a la sección PAGO en EASYBILL (https://app.easybill.pe/main#/company/pays) a continuación seleccionar Crear Pago y se mostrará la opción PagoEfectivo, puede seleccionar Depósitos en efectivo (pagos en cualquier agente) o Transferencia Bancaria(pagos por banca internet o banca móvil), luego de seleccionar cualquiera de los dos finalizar el pago haciendo click en Pagar, se generará un código CIP y debe realizar el pago.</p><p data-v-dfcd1b84>CULQUI: con la pasarela debe ingresar datos de su tarjeta para que se realice el pago. Para realizar el pago debes dirigirte a la sección PAGO en EASYBILL (https://app.easybill.pe/main#/company/pays) a continuación seleccionar Crear Pago y se mostrará la opción Tarjeta, en el formulario quese muestra debe ingresar el Número de tarjeta, mes de vencimiento de sutarjeta, año de vencimiento de su tarjeta, CVV y su correo electrónico, para finalizar su pago debe hacer click en Pagar y el pago ya se encuentra realizado. Los datos ingresados de su tarjeta no son verificados por MAKEASY S.R.L. por lo que no se responsabiliza por la información incorrecta que se ingrese.</p><p data-v-dfcd1b84>El usuario principal afirma y acepta bajo juramento que sus ingresos no tienen relación con ningún tipo de acto ilegal: lavado de   activos,narcotráfico, terrorismo u otro delito.</p></aside></div></div>"),_c('footer-section')],2)}
